import React, { Component } from 'react';
import './App.css';
import { Container, Row, Col, Button } from 'react-bootstrap';
import { Map, TileLayer, Marker, Popup } from 'react-leaflet';
//import Camera from 'react-html5-camera-photo';
//import 'react-html5-camera-photo/build/css/index.css';

class App extends Component {
  render() {
    return (
      <div className="App">
        <Container className="contenidor">
          <Row>
            <Col><Foto /></Col>
            <Col><Mapa /></Col>
          </Row>
        </Container>
        <BotoFoto />
      </div>
    );
  }
}

class Foto extends Component {
  constructor(props) {
    super(props)
    this.state = {
      imatge: 'https://upload.wikimedia.org/wikipedia/commons/7/7f/Nagec_cat.jpg'
    }
    updateFoto = updateFoto.bind(this);
  }
  render() {
    return (
      <div >
        <img className="foto" src={this.state.imatge} width="100px" height="100px" />
      </div>
    );
  }
}

function updateFoto(imatge) {
  this.setState({imatge});
}

class Mapa extends Component {
  constructor(props) {
      super(props)
      this.state = {
        lat: 41,
        lng: 1,
        zoom: 7,
        mapa: 'https://maps.wikimedia.org/osm-intl/{z}/{x}/{y}{r}.png'
      }
      updateMapa = updateMapa.bind(this);
  }
  render() {
    const position = [this.state.lat, this.state.lng]
    return (
      <Map center={position} zoom={this.state.zoom}>
        <TileLayer
          attribution=''
          url={this.state.mapa}
        />
        <Marker position={position}>
          <Popup>
            A pretty CSS3 popup. <br /> Easily customizable.
          </Popup>
        </Marker>
      </Map>
    )
  }
}

function updateMapa(lat, lng, zoom, mapa) {
  this.setState({lat, lng, zoom, mapa});
}

class BotoFoto extends Component {
  handleClick() {
    Mapa.setState({
      lat: 42,
      lng: 0,
      zoom: 8,
    })
  }
  render() {
    return (
      <div className="boto">
        <Button onClick={()=>actualitza()}>Actualitza</Button>
      </div>
    );
  }
}

function actualitza() {
  updateFoto('http://appinventor.mit.edu/explore/sites/all/files/ai2tutorials/helloPurr/kitty.png');
  updateMapa(42,0,5,'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png');
}

export default App;
